/* Smart grid */
/* Fonts */
@import url("https://fonts.googleapis.com/css?family=Lato");
@font-face {
  font-family: "Lato regular";
  src: url("./../fonts/latoregular.woff2") format("woff2"), url("./../fonts/latoregular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato light";
  src: url("./../fonts/latolight.woff2") format("woff2"), url("./../fonts/latolight.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Lato bold";
  src: url("./../fonts/latobold.woff2") format("woff2"), url("./../fonts/latobold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
/* Variables */
* {
  font-family: "Lato regular", sans-serif;
  box-sizing: border-box;
}

html,
body {
  background-color: #121212;
  font-family: "Lato regular", sans-serif;
  color: #fff;
  box-sizing: border-box;
}

.wrapper {
  max-width: 1410px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
  height: 100vh;
}
@media screen and (max-width: 2560px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 1410px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 1175px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 1100px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 960px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media screen and (max-width: 720px) {
  .wrapper {
    padding-left: 10px;
    padding-right: 10px;
  }
}
