  .wrapper {
    padding-left: 5px;
    padding-right: 5px;
  }
}

.d-flex {
  display: flex;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -7.5px;
  margin-right: -7.5px;
}

.col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 7.5px;
  margin-right: 7.5px;
}

.col-1 {
  width: calc(10% - 15px);
}

.col-2 {
  width: calc(20% - 15px);
}

.col-3 {
  width: calc(30% - 15px);
}

.col-4 {
  width: calc(40% - 15px);
}

.col-5 {
  width: calc(50% - 15px);
}

.col-6 {
  width: calc(60% - 15px);
}

.col-7 {
  width: calc(70% - 15px);
}

.col-8 {
  width: calc(80% - 15px);
}

.col-9 {
  width: calc(90% - 15px);
}

.col-10 {
  width: calc(100% - 15px);
}

.col-11 {
  width: calc(110% - 15px);
}

.col-12 {
  width: calc(120% - 15px);
}

.shift-1 {
  margin-left: calc(10% + 7.5px);
  margin-right: calc(10% + 7.5px);
}

.shift-2 {
  margin-left: calc(20% + 7.5px);
  margin-right: calc(20% + 7.5px);
}

.shift-3 {
  margin-left: calc(30% + 7.5px);
  margin-right: calc(30% + 7.5px);
}

.shift-4 {
  margin-left: calc(40% + 7.5px);
  margin-right: calc(40% + 7.5px);
}

.shift-5 {
  margin-left: calc(50% + 7.5px);
  margin-right: calc(50% + 7.5px);
}

.shift-6 {
  margin-left: calc(60% + 7.5px);
  margin-right: calc(60% + 7.5px);
}

.h2 {
  font-size: 25px;
}

.text-align-center {
